body {
  background-color: #333;
  font-family: Your Chosen Font, sans-serif;
}

.breed-select, .loader, .error, .cat-info {
  color: #fff;
  background-color: #444;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
}

.error {
  color: red;
}

.cat-info {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-top: 10px;
}

.cat-info p {
  text-align: center;
  margin: 10px 0;
}

.modal {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .7);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  max-width: 80%;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close {
  cursor: pointer;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.cat-info img {
  width: 800px;
  height: 600px;
  object-fit: cover;
}

.loader {
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  height: 4px;
  width: 0%;
  background: #ff3d00;
  animation: lineGrow 5s linear infinite;
  display: block;
}

@keyframes lineGrow {
  to {
    width: 100%;
  }
}

